@font-face {
  font-family: "PT Mono";
  src: url("../fonts/ptmono-regular.woff2") format("woff2"), url("../fonts/ptmono-regular.woff") format("woff");
  font-weight: 400;
  font-style: normal;
  font-display: swap;
}
@font-face {
  font-family: "Roboto Condensed";
  src: url("../fonts/roboto-condensed-regular.woff2") format("woff2"), url("../fonts/roboto-condensed-regular.woff") format("woff");
  font-weight: 400;
  font-style: normal;
  font-display: swap;
}
@font-face {
  font-family: "Roboto Condensed";
  src: url("../fonts/roboto-condensed-bold.woff2") format("woff2"), url("../fonts/roboto-condensed-bold.woff") format("woff");
  font-weight: 700;
  font-style: normal;
  font-display: swap;
}
*,
*::before,
*::after {
  box-sizing: border-box;
}

html,
body {
  margin: 0;
  padding: 0;
  min-height: 100vh;
}

html {
  font-style: normal;
  font-weight: 400;
  font-size: 16px;
  line-height: 22px;
  font-family: "PT Mono", "Courier New", monospace;
  color: #041827;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  text-rendering: optimizeLegibility;
}

body {
  width: 100%;
  height: 100%;
  background-color: #ffffff;
}

a {
  color: #041827;
  text-decoration: none;
}

img,
video {
  display: block;
  max-width: 100%;
  height: auto;
}

textarea {
  resize: none;
}

input:-webkit-autofill {
  box-shadow: inset 0 0 0 1000px #ffffff;
  -webkit-text-fill-color: #041827;
}

input,
textarea {
  border-radius: 0;
}
input::placeholder,
textarea::placeholder {
  opacity: 1;
}
input:invalid,
textarea:invalid {
  box-shadow: none;
}

select {
  border-radius: 0;
}

input::-ms-clear, input::-ms-reveal {
  display: none;
}

input[type=search]::-webkit-search-decoration,
input[type=search]::-webkit-search-cancel-button,
input[type=search]::-webkit-search-results-button,
input[type=search]::-webkit-search-results-decoration {
  appearance: none;
}

input::-webkit-outer-spin-button,
input::-webkit-inner-spin-button {
  margin: 0;
  appearance: none;
}

input[type=number] {
  appearance: textfield;
}

select,
textarea,
input:matches([type=email], [type=number], [type=password], [type=search], [type=tel], [type=text], [type=url]) {
  appearance: none;
}

button,
[type=button],
[type=reset],
[type=submit] {
  appearance: none;
}

.wrapper {
  position: relative;
  display: flex;
  flex-direction: column;
  min-height: 100vh;
  /* stylelint-disable-next-line */
  min-height: calc(100 * var(--vh, 1vh));
}
.wrapper .header,
.wrapper .footer {
  flex-shrink: 0;
}
.wrapper main {
  flex-grow: 1;
}

.rouble {
  font-family: "Rouble", "Arial", sans-serif;
  /* stylelint-disable */
  text-transform: lowercase !important;
  font-weight: 400 !important;
  /* stylelint-enable */
}

.visually-hidden {
  position: absolute;
  width: 1px;
  height: 1px;
  margin: -1px;
  padding: 0;
  overflow: hidden;
  white-space: nowrap;
  border: 0;
  clip: rect(0 0 0 0);
  clip-path: inset(100%);
}

.scroll-lock-ios {
  position: fixed;
  overflow: hidden;
}

.scroll-lock {
  overflow: hidden;
}

.no-scrollbar {
  -ms-overflow-style: none;
  scrollbar-width: none;
}
.no-scrollbar::-webkit-scrollbar {
  display: none;
}

.no-transition {
  /* stylelint-disable-next-line */
  transition: none !important;
}

.container {
  width: 100%;
  max-width: 1300px;
  margin: 0 auto;
  padding: 0 40px;
}
@media (max-width: 1023px) {
  .container {
    padding: 0 50px;
  }
}
@media (max-width: 767px) {
  .container {
    padding: 0 25px;
  }
}

.btn {
  display: inline-flex;
  padding: 10px 15px;
  color: #f2f5f7;
  background-color: rgba(255, 255, 255, 0);
  border: 1px solid #f2f5f7;
  border-radius: 3px;
  cursor: pointer;
  transition: opacity 0.3s ease;
}
.btn--red {
  background-color: #ff1553;
}
.btn--blue {
  padding: 14px 15px;
  width: 180px;
  color: #00b2ff;
  border: 1px solid #00b2ff;
}
.btn--black {
  padding: 15px;
  width: 265px;
  color: #ffffff;
  background-color: #041827;
}
.btn:disabled {
  opacity: 0.5;
  pointer-events: none;
}
@media (hover: hover) {
  .btn:hover:not(.focus-visible) {
    opacity: 0.8;
  }
}
.btn.focus-visible:focus {
  opacity: 0.8;
}

.modal {
  position: fixed;
  top: 0;
  right: 0;
  bottom: 0;
  left: 0;
  z-index: 1000;
  display: flex;
  flex-direction: column;
  overflow-y: auto;
  -webkit-overflow-scrolling: touch;
  background-color: rgba(3, 16, 27, 0.7);
  transform: scale(1.2);
  visibility: hidden;
  opacity: 0;
  transition: opacity 0.6s cubic-bezier(0.55, 0, 0.1, 1), transform 0.6s cubic-bezier(0.55, 0, 0.1, 1), visibility 0.6s cubic-bezier(0.55, 0, 0.1, 1);
  pointer-events: none;
  -ms-overflow-style: none;
  scrollbar-width: none;
}
.modal::-webkit-scrollbar {
  display: none;
}
.modal__wrapper {
  position: relative;
  display: flex;
  flex-shrink: 0;
  justify-content: center;
  align-items: center;
  width: 100%;
  min-height: 100%;
  padding: 60px 40px;
}
@media (max-width: 767px) {
  .modal__wrapper {
    padding: 60px 16px;
  }
}
.modal__overlay {
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  cursor: pointer;
}
.modal__content {
  position: relative;
  top: 0;
  left: -8px;
  width: 500px;
  min-height: 610px;
  padding-top: 47px;
  padding-left: 40px;
  padding-right: 40px;
  padding-bottom: 45px;
  background-color: #00b2ff;
  border-radius: 3px;
}
@media (max-width: 767px) {
  .modal__content {
    width: 100%;
    padding: 20px;
  }
}
.modal__close-btn {
  position: absolute;
  top: 17px;
  right: 15px;
  z-index: 1;
  width: 40px;
  height: 40px;
  padding: 0;
  background: rgba(255, 255, 255, 0);
  fill: #ffffff;
  border: none;
  cursor: pointer;
}
.modal--responsive .modal__content {
  width: 100%;
}
.modal--fit-content .modal__content {
  width: auto;
}
@media (max-width: 767px) {
  .modal--fit-content .modal__content {
    width: 100%;
  }
}
.modal--no-scale {
  transform: none;
}
.modal--preload {
  transition: none;
}
.modal.is-active {
  transform: scale(1);
  visibility: visible;
  opacity: 1;
  pointer-events: auto;
}

.modal form {
  display: flex;
  flex-direction: column;
}

.modal h2 {
  margin: 0;
  margin-bottom: 14px;
  font-family: "Roboto Condensed", "Arial Narrow", sans-serif;
  font-style: normal;
  font-weight: 400;
  font-size: 30px;
  line-height: 22px;
  color: #ffffff;
}

.modal span {
  display: block;
  margin-bottom: 40px;
  color: #ffffff;
}

.modal input {
  width: 100%;
  padding: 15px;
  margin-bottom: 21px;
  color: #ffffff;
  background: rgba(4, 20, 39, 0.15);
  border: none;
  border-radius: 3px;
}
.modal input::placeholder {
  color: #ffffff;
}

.modal textarea {
  width: 100%;
  height: 155px;
  padding: 25px 15px 15px;
  margin-bottom: 25px;
  color: #ffffff;
  background: rgba(4, 20, 39, 0.15);
  border: none;
  border-radius: 3px;
}
.modal textarea::placeholder {
  color: #ffffff;
}

.modal__button {
  display: block;
  width: 100%;
  margin-bottom: 19px;
  padding-bottom: 13px;
  font-family: "Roboto Condensed", "Arial Narrow", sans-serif;
  font-style: normal;
  font-weight: 700;
  font-size: 16px;
  line-height: 19px;
  letter-spacing: 0.05em;
  text-transform: uppercase;
  border: none;
  border-radius: 3px;
}

.modal label[for=modal-personal-data] {
  position: relative;
  display: block;
  left: 9px;
  padding-left: 26px;
  font-weight: 400;
  font-size: 14px;
  line-height: 16px;
  color: #ffffff;
  opacity: 0.8;
  cursor: pointer;
}

.modal input[name=modal-agree] {
  position: absolute;
  bottom: 20px;
  left: -32px;
}

.modal input[name=modal-agree] + label[for=modal-personal-data]::before {
  content: "";
  position: absolute;
  top: 0;
  left: 0;
  width: 15px;
  height: 15px;
  border: 1px solid #ffffff;
  border-radius: 2px;
}

.modal input[name=modal-agree]:checked + label[for=modal-personal-data]::before {
  background-image: url("../img/svg/tick.svg");
  background-repeat: no-repeat;
  background-position: center center;
}

.modal input[name=modal-agree]:disabled + label[for=modal-personal-data] {
  color: rgba(4, 20, 39, 0.15);
}

.modal input[name=modal-agree]:focus + label[for=modal-personal-data]::before {
  border-color: #041827;
}

.modal input[name=modal-agree]:disabled + label[for=modal-personal-data]::before {
  border-color: rgba(4, 20, 39, 0.15);
}

.modal input[name=modal-agree]:checked:disabled + label[for=modal-personal-data]::before {
  opacity: 0.4;
}

.page-header {
  position: relative;
  padding-top: 30px;
  padding-bottom: 35px;
  background: linear-gradient(180deg, #041827 84.9%, rgba(4, 24, 39, 0) 100%);
  z-index: 1;
}
@media (max-width: 1023px) {
  .page-header {
    padding-top: 20px;
    padding-bottom: 25px;
  }
}
@media (max-width: 767px) {
  .page-header {
    padding-top: 10px;
    padding-bottom: 20px;
  }
}

.page-header__container {
  display: flex;
  align-items: center;
  justify-content: space-between;
  gap: 20px;
  padding-right: 100px;
}
@media (max-width: 1023px) {
  .page-header__container {
    padding-right: 70px;
  }
}
@media (max-width: 767px) {
  .page-header__container {
    padding-right: 35px;
  }
}

.page-header__logo-image--mobile {
  display: none;
}
@media (max-width: 767px) {
  .page-header__logo-image--mobile {
    display: block;
    padding-top: 5px;
  }
  .page-header__logo-image--desktop {
    display: none;
  }
}

.page-header__contacts {
  display: flex;
  align-items: center;
  justify-content: space-between;
  flex-wrap: wrap;
  gap: 20px;
  min-width: 460px;
  padding-bottom: 5px;
}
