    display: none;
  }
}

.page-header__button {
  display: block;
  padding: 10px 0;
  min-width: 160px;
  height: 40px;
  margin-bottom: 7px;
  font-family: "Roboto Condensed", "Arial Narrow", sans-serif;
  font-style: normal;
  font-weight: 700;
  font-size: 14px;
  line-height: 16px;
  letter-spacing: 0.05em;
  text-transform: uppercase;
  text-align: center;
}
@media (max-width: 1023px) {
  .page-header__button {
    display: none;
  }
}

.page-footer {
